@charset "UTF-8";
select,
textarea,
input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color] {
  --at-p: 10px;
  --at-w: 100%;
  --at-box-szg: border-box;
  width: var(--at-w);
  box-sizing: var(--at-box-szg);
  padding: var(--at-p);
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search]:not(.wp-block-search__input),
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local] {
  --at-h: 40px;
  height: var(--at-h);
}

input[type=submit] {
  --at-p: 6px 12px;
  --at-bg-cl: var(--wp--preset--color--primary);
  --at-bdr-cl: var(--wp--preset--color--primary);
  --at-bdr-sty: solid;
  --at-bdr-w: solid;
  --at-cl: var(--wp--preset--color--default);
  --at-cur: pointer;
  padding: var(--at-p);
  background: var(--at-bg-cl);
  color: var(--at-cl);
  border-color: var(--at-bdr-cl);
  border-width: var(--at-bdr-w);
  border-style: var(--at-bdr-sty);
  cursor: var(--at-cur);
}
input[type=submit]:hover, input[type=submit]:focus {
  --at-bg-cl: var(--wp--preset--color--quaternary);
  --at-bdr-cl: var(--wp--preset--color--quaternary);
}

.wp-block-heading a:not(.wp-element-button):not(.wp-block-button__link), .wp-block-comment-content a:not(.wp-element-button):not(.wp-block-button__link), .entry-content.wp-block-post-content a:not(.wp-element-button):not(.wp-block-button__link):not(.wp-block-query-pagination-previous):not(.page-numbers):not(.wp-block-query-pagination-next), p a:not(.wp-element-button):not(.wp-block-button__link) {
  text-decoration: underline;
}

.pwp-txt-dec-non a, .pwp-txt-dec-non {
  text-decoration: none !important;
}

ol {
  list-style-position: inside;
}
ol li ul,
ol li ol {
  --at-m: 0 0 0 25px;
  margin: var(--at-m);
}

ul {
  list-style-position: inside;
  --at-m: 0;
  --at-p: 0;
  margin: var(--at-m);
  padding: var(--at-p);
}
ul li ul,
ul li ol {
  --at-m: 0 0 0 25px;
  margin: var(--at-m);
}

hr:not(.wp-block-separator) {
  --at-m: 15px 0;
  margin: var(--at-m);
}

.post-password-form input[type=submit] {
  --at-m: 15px 0 0 0;
  margin: var(--at-m);
}

.pwp-child-reset > * {
  margin-block-start: 0 !important;
}

:where(.wp-site-blocks) > * + * {
  margin-block-start: 0 !important;
}
:where(.wp-site-blocks) > * + * > * {
  margin-block-start: 0 !important;
}

.is-style-scroll-to-top .wp-element-button::after {
  content: "↑";
  --at-pos: absolute;
  --at-l: 50%;
  --at-t: 50%;
  --at-tf: translate(-50%, -50%);
  position: var(--at-pos);
  text-indent: 0;
  left: var(--at-l);
  top: var(--at-t);
  transform: var(--at-tf);
}

body:not(.admin-bar) .imsmoking-handler-theme-header {
  --at-t: 0;
}

.imsmoking-handler-theme-header {
  --at-pos: absolute;
  --at-t: var(--wp-admin--admin-bar--height);
  --at-l: 0;
  --at-r: 0;
  --at-z-idx: 9;
}
.imsmoking-handler-theme-hero-banner {
  --at-pos: relative;
}
@media (max-width: 575px) {
  .imsmoking-handler-theme-hero-banner {
    min-height: 80vh !important;
  }
}
@media (max-width: 575px) {
  .imsmoking-handler-theme-hero-banner .wp-block-cover__image-background {
    object-fit: contain !important;
    object-position: 0% 100% !important;
  }
}
.imsmoking-handler-theme-hero-banner-content {
  --at-pos: relative;
  --at-z-idx: 2;
}
@media (min-width: 1200px) {
  .imsmoking-handler-theme-hero-banner-content {
    margin-top: calc(var(--wp--preset--spacing--80) * -1);
  }
}
.imsmoking-handler-theme-hero-banner .at-svg {
  --at-cl: var(--wp--preset--color--secondary);
  --at-pos: absolute;
  max-width: 100%;
}
.imsmoking-handler-theme-is-h-w-40 {
  --at-h: 40px;
  --at-w: 40px;
}
.imsmoking-handler-theme-is-h-w-70 {
  --at-h: 70px;
  --at-w: 70px;
}
.imsmoking-handler-theme-shape-t {
  --at-t: 0;
  --at-z-idx: 1;
  --at-l: 0;
  --at-r: 0;
  --at-h: 120px;
}
.imsmoking-handler-theme-shape-b {
  --at-b: 0;
  --at-z-idx: 1;
  --at-l: 0;
  --at-r: 0;
  --at-h: 120px;
}